import { Projek_anggota } from 'src/app/sdk/hr/models/projek-_anggota';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { DirektoriProjekService, DirektoriPegawaiMasterService, DirektoriProjekAnggotaService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { Pegawai_master, Projek } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriProjekService: DirektoriProjekService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private direktoriProjekAnggotaService: DirektoriProjekAnggotaService,
        private singleUploadService: SingleUploadService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pesan: PesanService,
        private router: Router,
        private routerActive: ActivatedRoute,
        private fb: FormBuilderTypeSafe
    ) { }


    dataProjekAnggota: Projek_anggota[] = []
    dataProjek: Projek = {}
    id_projek: string = ''

    listPegawai: Pegawai_master[] = []
    toogleUpload = false;

    ngOnInit() {
        this.id_projek = this.routerActive.snapshot.paramMap.get('id_projek')

        this.form()
        this.getData()
        this.getListPegawai()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'hr_project.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }


    getListPegawai() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data;
            }
        )
    }

    id_projek_anggota: string = '';
    tambahAnggota() {
        if (this.id_projek_anggota == '') {
            this.notification.blank(
                'Notification Title',
                'This is the content of the notification. This is the content of the notification. This is the content of the notification.'
            );
            return false;
        }

        this.direktoriProjekAnggotaService.postDirektoriProjekAnggotas({ id_pegawai: this.id_projek_anggota, id_projek: this.id_projek }).subscribe(
            res => {
                this.getAnggota();
            }
        )
    }

    listAnggota: any[] = []
    getAnggota() {
        let filter = {
            id_projek: this.id_projek
        }
        this.direktoriProjekAnggotaService.getDirektoriProjekAnggotas(JSON.stringify(filter)).subscribe(
            res => {
                this.listAnggota = res.data;
            }
        )

    }
    deleteAnggota(id) {
        this.spinner.show()
        this.direktoriProjekAnggotaService.deleteDirektoriProjekAnggotasId(id).subscribe(
            data => {
                this.getAnggota()
                this.spinner.hide()
            }
        )
    }

    getData() {
        this.spinner.show()
        this.direktoriProjekService.getDirektoriProjeksId(this.id_projek).subscribe(
            data => {
                this.dataForm.setValue(data.data);
                this.getAnggota()
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "hr_project",
                    for_table: "erp_hr.projek",
                    for_module_id: data.data.id_projek
                })
            }
        )
    }

    dataForm: FormGroupTypeSafe<Projek>;
    form() {
        this.dataForm = this.fb.group<Projek>(
            {
                nama_projek: new FormControl('', [Validators.required]),
                keterangan_projek: new FormControl(''),
                owner: new FormControl(''),
                tanggal_mulai_projek: new FormControl(''),
                tanggal_selesai_projek: new FormControl(''),
                nilai_tipe: new FormControl(''),
                nilai_projek: new FormControl(''),
                prioritas: new FormControl(''),
                id_projek: new FormControl(''),
                nomor_kontrak: new FormControl(''),
                created_at: new FormControl(''),
                currency: new FormControl(''),
                updated_at: new FormControl(''),
                validasi: new FormControl(''),
                tanggal_kontrak: new FormControl(''),
                id_pegawai_leader: new FormControl(''),
                id_dokumen: new FormControl(''),
                id_user: new FormControl(''),
                id_vendor: new FormControl(''),
                id_data_flow_detail: new FormControl(''),
                id_data_flow: new FormControl(''),
                status: new FormControl(''),
                posisi: new FormControl(''),
                nomor: new FormControl(''),


            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }


        this.dataProjek = this.dataForm.value;
        this.direktoriProjekService.putDirektoriProjeksIdResponse({ id: this.id_projek, body: this.dataProjek }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )
    }


    goToBack() {
        this.router.navigate(['/hr/direktori/development/proyek/list'])
    }

}
