import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { Pegawai } from 'src/app/sdk/hr/models/pegawai';
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services/direktori-pegawai.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { DirektoriProjekService, DirektoriProjekAnggotaService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { Projek, Projek_anggota } from 'src/app/sdk/hr/models';
import { ToolsLogFlowService } from 'src/app/sdk/user/services/tools-log-flow.service';


@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private direktoriProjekService: DirektoriProjekService,
        private direktoriProjekAnggotaService: DirektoriProjekAnggotaService,
        private spinner: NgxSpinnerService,
        private routerActive: ActivatedRoute,
        private router: Router,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private standarApprovalService: StandarApprovalService,
        private toolsLogFlowService: ToolsLogFlowService,
    ) { }

    id_projek: string = ''
    dataProjek: Projek = {}
    dataPegawai: Pegawai = {}
    dataProjekAnggota: Projek_anggota[] = []

    ngOnInit() {
        this.routerActive.paramMap.subscribe(
            params => {
                console.log(params.get('id_projek'))
                if (params.get('id_projek')) {
                    this.id_projek = params.get('id_projek')
                }

                this.getDetailProjek()
                this.getProjekAnggota()
                this.getLog()

            }
        )



        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailProjek()
                    this.getProjekAnggota()
                    this.getLog()
                }
            }
        )

    }

    params: standarFlow = null;
    id_data_flow: string = ''
    //-----projek------
    getDetailProjek() {
        this.spinner.show()
        this.direktoriProjekService.getDirektoriProjeksId(this.id_projek).subscribe(
            data => {
                this.dataProjek = data.data;
                this.getPegawai(data.data.id_pegawai_leader);

                this.params = {
                    id: data.data.id_projek,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_projek"
                }
                this.standarApprovalService.param.next(this.params)
            }
        )
    }

    getPegawai(id_pegawai_leader: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai_leader).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }

    //----projek-anggota------//

    getProjekAnggota() {
        this.direktoriProjekAnggotaService.getDirektoriProjekAnggotas(JSON.stringify({ id_projek: this.id_projek })).subscribe(
            res => {
                this.dataProjekAnggota = res.data;
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_projek })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/development/proyek/list'])
    }

}
